import React from 'react';
import { Link } from 'react-router-dom';
import {

    SidebarContainer,
    NameTitle,
    TextLarge,
    TextMedium,
    ContactSidebarCont,
    FieldCont,
    Icon,
    ExternalLink,
    NavSidebarContainer,

} from './wpComponents';

const contactInfo = [

    { 
        
        icon: "icon-linkedin",
        href: "https://www.linkedin.com/in/mirsad02/",
        text: "/in/mirsad02"
    
    },
    { 
        
        icon: "icon-github",
        href: "https://github.com/rosinant3",
        text: "/rosinant3"
    
    },
    {

        icon: "icon-envelope",
        href: "mailto:mirsad.avdic22@gmail.com",
        text: "mirsad.avdic22@gmail.com"

    }

];

const renderContact = () => {

    return contactInfo.map((info:any)=>{

        const icon = info.icon;
        const href = info.href;
        const text = info.text;

        return <FieldCont key={info.icon}>
        <Icon className={icon}></Icon>
        <TextMedium>
            <ExternalLink 
            
                href={href}
                target="_blank" 
                rel="noopener noreferrer"
                
            >{text}
            </ExternalLink>
        </TextMedium>
        </FieldCont>;

    });

}
 
interface HomeProps {


}

const ProjectsSidebar: React.FC<HomeProps> = () => {

    return (<SidebarContainer>
        <NameTitle>
            <TextLarge>Mirsad Avdic</TextLarge>
            <TextMedium>Bachelor of Science in Safety Engineering</TextMedium>
        </NameTitle>   
        <ContactSidebarCont>
            {renderContact()}
        </ContactSidebarCont>
        <NavSidebarContainer>
            <Link to={`/`} >Home</Link>
            <Link style={{ opacity: 0.5 }} to={`/webprojects`} >Web Projects</Link>
        </NavSidebarContainer>
    </SidebarContainer>);


}

export default ProjectsSidebar;