import React, { Suspense } from 'react';
import {

    PresentationContainer,
    InfoContainer,
    InfoTitle,
    InfoDetails,
    InfoRepository,

} from './presentationComponents';
import Slideshow from './slideshow';
import VideoPlayer from './videoPlayer/videoPlayer';

const RosComp = React.lazy(()=>{
 
    return import(`./rosinante/App`);
  
  });
  
interface PresentationProps {

    item: any;
    dispatch: any;

}

const Presentation: React.FC<PresentationProps> = (props) => {
 
    const item = props.item;
    const name = item.name;
    const photos = item.photos;
    const videoId = item.videoId;
    const repository = item.repository;
    const description = item.description;
    let video: any;
 
    window.scrollTo({top: 0});

    if (videoId) {

        video = <VideoPlayer info={{ videoId: videoId }} />;

    }

    if (name === "rosinante") {

        return (<Suspense fallback={<div></div>}>
            <RosComp />
          </Suspense>);

    } 

    if (name !== "rosinante") {

        return (<PresentationContainer>
            <InfoContainer>
                <InfoTitle>{name}</InfoTitle>
                <InfoDetails>{description}</InfoDetails>
                <InfoRepository
                
                    href={repository}
                    target="_blank" 
                    rel="noopener noreferrer"
                
                >{repository}</InfoRepository>
            </InfoContainer>
            <Slideshow items={photos} name={name} />
            {video}
        </PresentationContainer>);


    }

    return <div></div>;

}

export default Presentation;