import React, { useState, useEffect } from 'react';
import {

    ProjectsContainer,


} from './projectsComponents';
import Presentation from './presentation/presentation';
import Project from './project';
import queryString from 'query-string';
import { useLocation } from 'react-router-dom';

const renderProjects = (projects: any) => {

    return projects.map((project: any) => {

        return <Project info={project} key={project.id} />;

    });

};
 
interface ProjectsProps {

    state: any;
    dispatch: any;

}

const Projects: React.FC<ProjectsProps> = (props) => {
 
    const location = useLocation();
    const state = props.state;
    const dispatch = props.dispatch;
    const projects = state.projects;
    const [ active, setActive ] = useState(-1);
    const parsed = queryString.parse(location.search);
    const p: any = parsed.p;
    let content: any;
    window.scrollTo({top: 0});

    useEffect(()=>{

        let found = false;

        for (let i = 0; i < projects.length; i++) {

            let item = projects[i];
    
            if (item.active) {
    
                setActive(i);
                found = true;
                break;
    
            }
    
        }

        if (found === false) {

            setActive(-1);

        }

        if (p && active === - 1) {

            dispatch({ type: "setActive", p: p });
    
        }

        if (!p && active !== -1) {

            dispatch({ type: "setActive", p: "" });

        }

    }, [active, projects, location, dispatch, p]);

    if (active !== -1) {

        content = <Presentation item={projects[active]} dispatch={dispatch} />;

    }

    if (active === -1 && !p) {

        content = <ProjectsContainer>{renderProjects(projects)}</ProjectsContainer>;

    }

    return (<React.Fragment>{content}</React.Fragment>);


}

export default Projects;