import React, { useState, useEffect, useRef } from 'react';
import {

    SlideshowContainer,
    LargeContainer,
    LargeImage,
    LargeImageLoad,
    SlideshowOtherImagesContainer,
    SildeshowOtherImage,
    SlideshowMainImage,
    FullScreenComp,


} from './presentationComponents';

const onTouchEnd = (e: any, position: any, dispatch: any, active: number, itemsLen: number) => {

    const end = e.changedTouches[0].clientX;
    const x = position.x;
    const y = position.y;
    const clutchY = Math.abs(y - e.changedTouches[0].clientY);
    const clutchX = Math.abs(x - e.changedTouches[0].clientX);

	if ( clutchY < 50 && clutchX > 50) {

	    if (x < end) {
			// LEFT
            if (active === 0) {

                dispatch(itemsLen);

            } else {

                dispatch(active - 1);

            }
			   

		}
      
        if (x > end) {
        // RIGHT
            if (active === itemsLen) {

                dispatch(0);

            } else {

                dispatch(active + 1);

            }
      
  
        }

	}

}


interface SlideshowProps {

    items?: any;
    name?: any;
    active?: any;
    setActive?: any;
    item?: any;
    index?: any;
    container?: any;

}

const LargeItem: React.FC<SlideshowProps> = (props) => {
 
    const ref: any = useRef(null);
    const src = props.item;
    const index = props.index;
    const active = props.active;
    const items = props.items;
    const name = props.name;
    const [ opacity, setOpacity ] = useState(0);
    const [ loaded, setLoaded ] = useState(false);
    const [ position, setPosition ] = useState({ x: 0, y: 0 });
    const setActive = props.setActive;
    const [ fullScreen, setFullScreen ] = useState(-1);
    let fullComponent: any;

    useEffect(()=>{

        if (loaded) {

            if (index === active) {

                setOpacity(1);
                

            } else {

                setOpacity(0);

            }

        }


    }, [index, active, loaded]);

    if (fullScreen !== -1) {

        fullComponent = <FullScreenComp 
        
            style={{ backgroundImage: `url('/projects/${name}/${items[active]}')` }}
            onClick={()=>{ setFullScreen(-1); }}
        
        
        ></FullScreenComp>;

    }

    return (<React.Fragment>
        <LargeImageLoad 
        
            src={src}
            onLoad={()=>{  if (ref.current) { setLoaded(true); }}} 
            onError={()=>{ if (ref.current) { setLoaded(true); }}}
            ref={ref}
        
        />
        <LargeImage 
        
            style={{ 
                
                backgroundImage: `url('${src}')`, 
                opacity: opacity 
                
            }}
            onClick={()=>{

                setFullScreen(index);

            }}
            onTouchStart={(e)=>{ setPosition({ x: e.targetTouches[0].pageX, y: e.targetTouches[0].pageY}); }}
            onTouchEnd={(e)=>{ onTouchEnd(e, position, setActive, active, items.length - 1) }}    
            
        ></LargeImage>
        {fullComponent}
    </React.Fragment>);
 

}

const Image: React.FC<SlideshowProps> = (props) => {

    const ref: any = useRef(null);
    const container = props.container;
    const dispatch = props.setActive;
    const [ loaded, setLoaded ] = useState(false);
    const src = props.item;
    const active = props.active;
    const index = props.index;
    let opacity = 0;
  
    if (loaded) {
  
      opacity = 1;
  
    }
  
    if (index === active) {
  
      if (ref.current && container.current) {
  
          const leftPos = ref.current.offsetLeft;
          let offset = 450;
  
          if (window.innerWidth <= 650) {
  
            offset = 100;
  
          }
 
          container.current.scrollTo({left: leftPos - offset, behavior: 'smooth'});
  
  
      }
  
   
  
    }
   
    return (<SildeshowOtherImage ref={ref} onClick={()=>{
   
              dispatch(index);
  
              }} >
              <img style={{display: "none", opacity: opacity}} alt="" src={src} onLoad={()=>{
  
                if (ref.current) {
  
                  setLoaded(true);
  
                }
  
  
               }} onError={()=>{
  
                if (ref.current) {
  
                  setLoaded(true);
  
                }
  
  
               }}/>
              <SlideshowMainImage style={{ opacity: opacity, backgroundImage: `url(${src})` }} >
              </SlideshowMainImage>
  
            </SildeshowOtherImage>);
  
  }

const renderItems = (items: any, name: any, active: any, setActive: any) => {

    return items.map((item:any, index: number)=>{

        const directory = `/projects/${name}/${item}`

        return <LargeItem key={item} name={name} index={index} items={items} item={directory} active={active} setActive={setActive}/>;

    })

}

const renderSmallItems = (items: any, name: any, active: any, setActive: any, ref: any) => {

    return items.map((item:any, index: number)=>{

        const directory = `/projects/${name}/${item}`

        return <Image container={ref} key={item} index={index} items={items} item={directory} active={active} setActive={setActive} />;

    })

}

const Slideshow: React.FC<SlideshowProps> = (props) => {
 
    const ref: any = useRef(null);
    const items = props.items;
    const name = props.name;
    const [ active, setActive ] = useState(0);


    return (<SlideshowContainer>
        <LargeContainer>
            {renderItems(items, name, active, setActive)}
        </LargeContainer>
        <SlideshowOtherImagesContainer ref={ref}>
            {renderSmallItems(items, name, active, setActive, ref)}
        </SlideshowOtherImagesContainer>
    </SlideshowContainer>);

}

export default Slideshow;