import React, { useState, useRef } from 'react';
import { Link } from 'react-router-dom';
import {

    ProjectContainer,
    ProjectThumbnail,
    ProjectThumbnailLoad,
    NameTitle,
    OpenCont,
    TextLarge,
    TextMedium,
    TextOpen,
    BlankLink,


} from './projectsComponents';
 
interface ProjectProps {

    info: any;

}

const Project: React.FC<ProjectProps> = (props) => {
 
    const ref: any = useRef(null);
    const [ opacity, setOpacity ] = useState(0);
    const info = props.info;
    const photos = info.photos;
    const name = info.name;
    const description = info.description;
    const repository = info.repository;
    const online = info.online;
    const directory = `/projects/${name}/`;
    const thumbnail = `${directory}${photos[0]}`;
    let link: any = <Link to={`/webprojects?p=${name}`}>Open</Link>;


    if (
        
        name === "iris" ||
        name === "osiris" ||
        name === "fable" ||
        name === "sunstone"
        
        
        ) {

        link = <BlankLink 
        
            href={repository}
            target="_blank" 
            rel="noopener noreferrer"
        
        >Open</BlankLink>;

    }

    if (

        name === "kravmagatuzla" ||
        name === "oslipnica"

    ) {

        link = <BlankLink 
        
            href={online}
            target="_blank" 
            rel="noopener noreferrer"
    
        >Open</BlankLink>;


    }
 
    return (<ProjectContainer ref={ref}>
        <ProjectThumbnailLoad 

            src="/profile.jpg" 
            onLoad={()=>{ if (ref.current) { setOpacity(1); } }} 
            onError={()=>{ if (ref.current) { setOpacity(1); } }}

        />
        <ProjectThumbnail style={{ opacity: opacity, backgroundImage: `url('${thumbnail}')` }}></ProjectThumbnail>
        <NameTitle>
            <TextLarge>{name}</TextLarge>
            <TextMedium>{description}</TextMedium>
        </NameTitle>
        <OpenCont>
            <TextOpen>{link}</TextOpen>
        </OpenCont>
    </ProjectContainer>);


}

export default Project;