import styled from 'styled-components';


export const VideoPlayerContainer = styled.div`

    padding: 1rem;
    font-size: 0.9rem;
    position: relative;
    opacity: 1;
    color: #501;
    min-height: 80vh;
    cursor: default;
    text-align: center;
    margin-bottom: 5rem;

    iframe {

        margin-top: 1rem;

    }

    > a {

        text-decoration: none;
        opacity: 0.5;
        padding: 1rem;
        color: #501;
        font-weight: bold;
        font-style: italic;
        font-size: 0.9rem;

    }

    @media only screen and (max-width: 750px) {

        padding: 0rem;
        padding-bottom: 2rem;
          
    }

`;