import styled from 'styled-components';

export const ProjectsContainer = styled.div`

    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto;
    grid-gap: 2rem;
    padding: 2rem;

    @media (max-width: 1070px) {

        grid-template-columns: 1fr;
        grid-gap: 1rem;
        padding: 1rem;

    }


`;

export const ProjectContainer = styled.div`

    margin-bottom: 1rem;

`;

export const ProjectThumbnail = styled.div`

    height: 250px;
    width: 100%;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    background-color: rgba(95, 162, 161, 1);
    transition-property: opacity;
    transition-duration: 500ms;

`;

export const ProjectThumbnailLoad = styled.img`

    display: none;

`;
 
export const OpenCont = styled.div`

    margin-top: 0.5rem;

`;

export const NameTitle = styled.div`

    display: flex;
    flex-direction: column;
    margin-top: 0.5rem;

`;

export const TextLarge = styled.div`

    padding: 0.25rem;
    font-size: 1.2rem;
    color: rgba(95, 162, 161, 1);
    font-weight: bold;

`;

export const TextOpen = styled.div`

    padding: 0.25rem;
    font-size: 0.9rem;
    color: rgba(95, 162, 161, 1);
    font-weight: bold;
    display: flex;
    justify-content: flex-end;

    > a {

        color: rgba(95, 162, 161, 1);
        text-decoration: none;
        transition-property: opacity;
        transition-duration: 1s;
        
        &:hover {

            opacity: 0.5;

        }

    }

`;

export const BlankLink = styled.a`

    color: rgba(95, 162, 161, 1);
    text-decoration: none;
    transition-property: opacity;
    transition-duration: 1s;

    &:hover {

        opacity: 0.5;

    }

`;

export const TextMedium = styled.div`

    padding: 0.25rem;
    font-size: 0.9rem;
    color: rgba(95, 162, 161, 1);
    font-style: italic;

    > a {

        color: rgba(95, 162, 161, 1);
        text-decoration: none;
        font-style: normal;
        transition-property: opacity;
        transition-duration: 1s;
        
        &:hover {

            opacity: 0.5;

        }

    }

`;