import styled from 'styled-components';

export const WPContainer = styled.div`

    display: grid;
    grid-template-columns: 250px auto;
    grid-template-rows: auto;
    min-height: 100vh;

    @media (max-width: 650px) {

        grid-template-columns: auto;

    }


`;

export const SidebarContainer = styled.div`

    padding: 1rem;
    text-align: center;
    background-color: rgba(95, 162, 161, 1);
    word-break: break-word;


`;

export const NameTitle = styled.div`

    display: flex;
    flex-direction: column;
    margin-top: 0.5rem;

`;

export const TextLarge = styled.div`

    padding: 0.25rem;
    font-size: 1.2rem;
    color: white;
    font-weight: bold;

`;

export const TextMedium = styled.div`

    padding: 0.25rem;
    font-size: 0.9rem;
    color: white;
    font-style: italic;

`;

export const ContactSidebarCont = styled.div`

    display: flex;
    flex-direction: column;
    margin-top: 1rem;
    aling-items: center;

`;

export const FieldCont = styled.div`

    display: flex;
    flex-direction: row;
    margin-right: 1rem;

`;

export const ExternalLink = styled.a`

    color: white;
    text-decoration: none;
    transition-property: opacity;
    transition-duration: 1s;
    opacity: 1;

    &:hover {

        opacity: 0.5;

    }

`;

export const Icon = styled.div`

    color: white;
    font-size: 1.1rem;
    margin-right: 0.25rem;
    display: flex;
    align-items: center;

`;


export const NavSidebarContainer = styled.div`

    padding: 1rem;
    display: flex;
    align-items: center;
    flex-direction: column;

    a {

        color: white;
        text-decoration: none;
        font-weight: bold;
        transition-property: opacity;
        transition-duration: 1s;
        margin-bottom: 0.5rem;
        opacity: 1;
    
        &:hover {
    
            opacity: 0.5;
    
        }

    }

`;