import styled from 'styled-components';

export const PresentationContainer = styled.div`




`;

export const FullScreenComp = styled.div`

    position: fixed;
    top: 0px;
    left: 0px;
    right: 0px;
    bottom: 0px;
    background-color: rgba(95, 162, 161, 1);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    z-index: 100000;
    cursor: pointer;

`;

export const InfoContainer = styled.div`

    text-align: center;
    color: rgba(95, 162, 161, 1);
    word-break: break-all;
    padding: 1rem;
    word-wrap: break-word;

`;

export const InfoTitle = styled.div`

    padding: 0.5rem;
    font-size: 1.2rem;
    font-weight: bold;
    word-break: break-all;

`;

export const InfoDetails = styled.div`

    padding: 0.5rem;
    font-size: 0.9rem;
    font-style: italic;
    word-break: break-all;


`;

export const InfoRepository = styled.a`

    font-size: 0.8rem;
    text-decoration: none;
    color: rgba(95, 162, 161, 1);
    transition-property: opacity;
    transition-duration: 1s;
    word-break: break-all;

    &:hover {

        opacity: 0.5;

    }


`;

export const SlideshowContainer = styled.div`

    margin-top: 1rem;
    display: grid;
    grid-template-columns: auto;
    grid-template-rows: 400px 125px;
    margin-bottom: 2rem;

`;

export const LargeContainer = styled.div`

    position: relative;
    padding: 1rem;


`;

export const LargeImage = styled.div`

    top: 0rem;
    left: 0rem;
    right: 0rem;
    bottom: 0rem;
    position: absolute;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    background-color: rgba(95, 162, 161, 1);
    transition-property: opacity, background-color;
    transition-duration: 1s;
    border-top-style: solid;
    border-width: 1px;
    border-color: rgba(95, 162, 161, 1);
    cursor: pointer;

    &:hover {

        background-color: rgba(95, 162, 161, 0.5);

    }


`;

export const LargeImageLoad = styled.img`


    display: none;

`;

export const SlideshowOtherImagesContainer = styled.div`

    overflow: hidden;
    white-space: nowrap;
    box-sizing: border-box;
    grid-row: 2;
    height: 100%;
    border-bottom-style: solid;
    border-color: rgba(95, 162, 161, 1);
    border-width: 2px;
    background-color: rgba(95, 162, 161, 1);

    @media (max-width: 1100px) {



    }

`;

export const SildeshowOtherImage = styled.div`

    color: white;
    cursor: pointer;
    transition-property: opacity;
    transition-duration: 1s;
    opacity: 1;
    position: relative;
    height: 125px;
    display: inline-block;
    width: 200px;
    border-style: solid;
    border-width: 1px;
    border-color: rgba(95, 162, 161, 1);
    background-color: rgba(95, 162, 161, 1);
    box-sizing: border-box;

    &:hover {

        opacity: 0.5;

    }

    @media (max-width: 1100px) {

        
 
     }

     @media (max-width: 800px) {

        width: 100px;
        
 
     }

`;

export const SlideshowMainImage = styled.div`

    height: 100%;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    transition-property: opacity;
    transition-duration: 500ms;

`;